body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body{
  background-color: black;
}

.App{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content:center;
  padding-top: 15px;
  font-family: Lato, sans-serif;

}

.logo-contenedor{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.logo{
  height:50px;
}

.contenedor-principal{
  height: 550px;
  min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
button{
    width: 250px;
    height: 80px;
    font-family:  Roboto, sans-serif;
    font-weight: bold;
    color: white;
    border:  2px solid white;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
    font-size: 35px;
}

button:hover{
    background-color: aquamarine;
}

.boton-clic{
    background-color: blue;
}

.boton-reiniciar{
    background-color: purple;
}
.contador{
    min-width: 300px;
    height: 300px;
    font-size: 160px;
    padding: 25px;
    color: azure;
    display: flex;
    justify-content: center;
    border: 8px solid white;
    margin-bottom: 15px;
}
